<template>
    <div class="container-fluid main-content">
        <div class="col-md-3" style="padding:15px;">
            <h3>Music Now</h3>
            <p class="secondary-text">A page devoted to covering the hottest tracks as they drop. This
                                        is a strict NO J.COLE zone.<p>
            <h3>Songs of the Year</h3>
            <iframe src="https://embed.spotify.com/?uri=spotify%3Auser%3A1226191915%3Aplaylist%3A78xydTNUOusjhekwXivJdU&theme=white" width="100%" height="380" frameborder="0" allowtransparency="true"></iframe>
        </div>
        
        <div class="col-md-9" v-if='!dataLoading'>
        
        <div v-for='post in posts'>
            <div class="music-post card">
                <div>
                    <div style="float:left;position:relative;left:15px;" class="tertiary-color">{{post.category}}</div>
                    <div style="float:right;position:relative;right:15px;color:#F9A825">
                        <i class="material-icons">star</i>
                        <i class="material-icons">star</i>
                        <i class="material-icons">star</i>
                        <i class="material-icons">star</i>
                        <i class="material-icons">star</i>
                    </div>
                </div>
                    <div class="card-block" style="padding-top:35px;">
                        
                        <h3 style="position:relative;right:10px">{{post.album}}</h3>
                        <p class="card-text secondary-text">{{post.artist}}</p>
                        <router-link to="/" class="tertiary-color btn-flat">READ MORE</router-link>
                    </div>
                    <!-- Card footer -->
                    <div class="card-data" style="padding-top:10px;padding-bottom:0;">
                        <ul>
                            <li><i class="fa fa-clock-o"></i> {{post.date | moment}} <span style="margin-left:20px;"><i class="material-icons" style="position:relative;top:3px;font-size:18px;">comment</i> 5</span></li>
                        </ul>
                    </div>
             </div>
        </div>
             
        </div>
        
        <div class='col-md-9 secondary-text' v-if="dataLoading">
            <i class="material-icons" style="font-size:96px;position:fixed;top:40%;opacity:0.50">library_music</i>
        </div>
        
    </div>
</template>

<script>
import moment from 'moment'

    export default {
        name: 'music',
        filters: {
            moment: function (date) {
                return moment(date).format('MMMM Do YYYY, h:mm:ss a');
            }
        },
        data () {
            return{
            posts: [],
            dataLoading: false
            }
        },
        methods: {
            getMusicPosts: function (){
                preloader.on();
                this.dataLoading = true;
                axios.get('https://trash-server.herokuapp.com/blog_api/api/music')
                    .then(response => this.posts = response.data)
                    .then(_ =>{                
                        this.dataLoading = false;
                        preloader.off();
                    })
                
            }
        },
        created() {
            this.getMusicPosts()
        }
}
</script>

<style>
    .music-post{
        padding:10px;
    }
</style>


